import React, {useEffect, useState} from "react";
import styled from "styled-components";
import bgImg from "../assets/images/top_jgp.jpg"

const aa = require("../assets/voide/top.mov").default;
export const VodieWidget: React.FC = () => {

    const [height, setHeight] = useState(810);
    const [width, setwidth] = useState(0);
    const [postion, setPostion] = useState({left: 0, top: 0})

    let minHeight = 810;
    let minWidth = 1440;
    useEffect(() => {
        // eslint-disable-next-line no-restricted-globals
        let _height = screen.height
        let _width;
        let _postion = {
            left: 0,
            top: 0,
        };

        _width = _height * 1.7777;
        if (_height < 500) {
            setHeight(minHeight);
            _postion.top = (minHeight - _height) / 2;
            setwidth(minWidth);
        } else {
            setHeight(_height);
            setwidth(_width);
            _postion.left = (_width - document.body.clientWidth) / 2;
        }

        setPostion(_postion);

    }, [])
    return (
        <VodieStyle>
            <video className={'video'} style={{
                height: height + "px",
                width: width + 'px',
                left: (0 - postion.left) + "px",
                top: (0 - postion.top) + "px"
            }} autoPlay muted playsInline>
                <source src={aa}/>
            </video>


            <h3>The DeFi Treasury For Liquidity <span>Pool Tokens</span></h3>
            <h4>Unlock Liquidity from AMM Pools! Unbound is building the derivative layer of AMM's with First-Ever Debt-Free Liquidity Provision System. The Unbound Protocol charges no Interest and it's Liquidation Free. The Unbound Factory smart contract supports Uniswap, Balancer, Mooniswap, Bancor, Curve.fi etc</h4>
            <h5>Dashboard</h5>
            {/*<ReactPlayer  muted={true} playing={true} url={aa} loop={true} controls={false}/>*/}

        </VodieStyle>
    );
}
const VodieStyle = styled.div`
  position: relative;
  height: 100vh;
  overflow: hidden;
  //background-image: url("${bgImg}");
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  h3{
    max-width: 626px;
    font-size: 50px;
    font-weight: 400;
    color: #222222;
    text-align: center;
    line-height: 76px;
    span{
      font-weight: 700;
    }
  }
  h4{
    max-width: 853px;
    font-size: 21px;
    font-weight: 400;
    color: #666666;
    line-height: 31px;
    margin: 40px 0;
  }
  
  h5{
    padding: 10px 40px;
    cursor: pointer;
    background: #1482E4;
    border-radius: 10px;
    font-size: 20px;
    color: #F0F0F0;
  }
  
  .video {
    pointer-events: none;
    position: absolute;
    width: 100%;
    z-index: -1;
    //display: none;
  }

`
